.box {
    align-self: flex-end;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    background-color: orange;
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    text-color: white;
}
.bounce-7 {
    -webkit-animation-name: bounce-7;
            animation-name: bounce-7;
    -webkit-animation-timing-function: cubic-bezier(0.280, 0.840, 0.420, 1);
            animation-timing-function: cubic-bezier(0.280, 0.840, 0.420, 1);
}
@-webkit-keyframes bounce-7 {
    0%   { -webkit-transform: scale(1,1)      translateY(0); transform: scale(1,1)      translateY(0); }
    10%  { -webkit-transform: scale(1.1,.9)   translateY(0); transform: scale(1.1,.9)   translateY(0); }
    30%  { -webkit-transform: scale(.9,1.1)   translateY(-15px); transform: scale(.9,1.1)   translateY(-15px); }
    50%  { -webkit-transform: scale(1.05,.95) translateY(0); transform: scale(1.05,.95) translateY(0); }
    57%  { -webkit-transform: scale(1,1)      translateY(-7px); transform: scale(1,1)      translateY(-7px); }
    64%  { -webkit-transform: scale(1,1)      translateY(0); transform: scale(1,1)      translateY(0); }
    100% { -webkit-transform: scale(1,1)      translateY(0); transform: scale(1,1)      translateY(0); }
}
@keyframes bounce-7 {
    0%   { -webkit-transform: scale(1,1)      translateY(0); transform: scale(1,1)      translateY(0); }
    10%  { -webkit-transform: scale(1.1,.9)   translateY(0); transform: scale(1.1,.9)   translateY(0); }
    30%  { -webkit-transform: scale(.9,1.1)   translateY(-15px); transform: scale(.9,1.1)   translateY(-15px); }
    50%  { -webkit-transform: scale(1.05,.95) translateY(0); transform: scale(1.05,.95) translateY(0); }
    57%  { -webkit-transform: scale(1,1)      translateY(-7px); transform: scale(1,1)      translateY(-7px); }
    64%  { -webkit-transform: scale(1,1)      translateY(0); transform: scale(1,1)      translateY(0); }
    100% { -webkit-transform: scale(1,1)      translateY(0); transform: scale(1,1)      translateY(0); }
