#event_detail_page_container{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(/static/media/event_background.fa107d95.png);
    background-repeat: repeat-y;
    background-attachment: fixed;
    background-size: fixed;
    display: cover;
    flex-direction: column;
    align-items: center;
    font-family: 'Balsamiq Sans', cursive, Pacifico;
}

#event_detail_page_body{
    width: 800px;
    height: auto;
    background-color: rgba(1, 1, 1, 0.7);
    border-radius: 50px;
    border: 1px solid black;
    padding: 30px;
    justify-content: center;
    align-items: center;
}


#event_detail_page_body .data_body{
    display: flex;
    flex-direction: row;
    font-size: 1.4rem;
    color: white;
    margin-bottom: 5px;
}

#event_detail_page_body label{
    width: 200px;
}

#event_detail_page_body span{
    font-size: 1.3rem;
    width:300px;
    color: white;
}

#event_detail_page_body button{
    background-color: orange;
    border-radius: 5px;
    padding: 3px;

}

#event_detail_page_container #description{
    border: 4px solid orange;
    padding: 5px;
}

#event_detail_page_container #go_back_event{
    font-size: 2rem;
    position: relative;
    left: 600px;
}
#event_form_container{
    padding-top: 20px;
    width: 350px;
    height: 700px;
    background-color: rgba(255,255,255, 0.9);
    border-radius: 25px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    z-index: 10;
    position: fixed;
    right: 30px;
    top: 150px;
    visibility: hidden;
    opacity: 0;
}

#event_form_exit{
    text-decoration: none;
    position: absolute;
    font-size: 2rem;
    right: 1rem;
    top: 0;
    width: 40px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
}

#event_form_container:target{
    visibility: visible;
    opacity: 1;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
}

#event_form_button:checked ~ #event_form_container{
    visibility: visible;
}
